.hamburger {
  display: flex;
  align-self: center;
  flex-direction: column;
  justify-content: space-between;
  width: 24px;
  height: 24px;
  cursor: pointer;
  padding-top: 5px;
  padding-bottom: 5px;
}

.hamburger span {
  /* align-self: flex-end; */
  height: 2px;
  width: 83.33333333%;
  background: #07335d;
  transition: all 400ms ease-in-out;
}

.hamburger .meat {
  width: 100%;
  transition: all 200ms ease-in-out;
}

.hamburger .bottom-bun {
  width: 58.33333333%;
  transition: all 100ms ease-in-out;
}

.hamburger:hover span {
  width: 100%;
}

html:not(.body-menu-opened) .hamburger:hover .top-bun {
  -webkit-animation: burger-hover 1s infinite ease-in-out alternate forwards 200ms;
  animation: burger-hover 1s infinite ease-in-out alternate forwards 200ms;
}

html:not(.body-menu-opened) .hamburger:hover .meat {
  -webkit-animation: burger-hover 1s infinite ease-in-out alternate forwards 400ms;
  animation: burger-hover 1s infinite ease-in-out alternate forwards 400ms;
}

html:not(.body-menu-opened) .hamburger:hover .bottom-bun {
  -webkit-animation: burger-hover 1s infinite ease-in-out alternate forwards 600ms;
  animation: burger-hover 1s infinite ease-in-out alternate forwards 600ms;
}

@-webkit-keyframes burger-hover {
  0% {
    width: 100%;
  }
  50% {
    width: 50%;
  }
  100% {
    width: 100%;
  }
}

@keyframes burger-hover {
  0% {
    width: 100%;
  }
  50% {
    width: 50%;
  }
  100% {
    width: 100%;
  }
}

.body-menu-opened .hamburger .top-bun {
  transform: rotate(-45deg) translate(-4.5px, 4px);
  width: 100%;
}

.body-menu-opened .hamburger .bottom-bun {
  transform: rotate(45deg) translate(-4.5px, -4px);
  width: 100%;
}

.body-menu-opened .hamburger .meat {
  width: 0;
}

.menu {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  z-index: 990;
  background: #ffffff;
  max-width: 100%;
  width: 100%;
  padding: 4.25rem 4.375rem 4.1875rem;
  display: flex;
  flex-direction: column;
  transition: transform 0.4s ease-in-out;
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
}

.menu:before {
  position: absolute;
  content: "";
  right: 0px;
  top: 0px;
  width: 160px;
  height: 160px;
  background: url(../images/icons/pattern-8.png) no-repeat;
}

.body-menu-opened .menu {
  -webkit-transform: translateX(0%);
  transform: translateX(0%);
}

.hide-menu {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 100%;
  background: rgba(0, 0, 0, 0.65);
  z-index: 980;
}

.body-menu-opened .hide-menu {
  bottom: 0;
}

@media (min-width: 576px) {
  .menu {
    width: 34rem;
  }
}

.menu .menu-lang,
.menu .menu-main,
.menu .social,
.menu .menu-footer {
  opacity: 0;
  transform: translateY(3rem);
  transition: all 0.5s ease-in-out;
}

.menu .menu-lang {
  transition-delay: 0.2s;
}

.menu .menu-main {
  transition-delay: 0.4s;
  margin-top: 50px;
}

.menu .social {
  transition-delay: 0.6s;
}

.menu .menu-footer {
  transition-delay: 0.8s;
}

.body-menu-opened .menu .menu-lang,
.body-menu-opened .menu .menu-main,
.body-menu-opened .menu .social,
.body-menu-opened .menu .menu-footer {
  opacity: 1;
  transform: translateY(0);
}

