  left: 50px;
  transform: translate(0, -50%);
}

.progress-nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.progress-nav li {
  width: 8px;
  height: 8px;
  margin: 10px 0px;
  background: #ffffff !important;
  overflow: hidden;
  border: 1px solid #5a7797;
  transition: background 0.5s ease-in-out;
  position: relative;
}

.progress-nav li a {
  width: 8px;
  height: 8px;
  display: inline-block;
  position: relative;
}

.progress-nav li:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: #464a8a;
}

.progress-nav li.active:before {
  animation: navHeight 0.5s ease-in-out forwards;
}

@-webkit-keyframes navHeight {
  0% {
    height: 0;
  }
  100% {
    height: 100%;
  }
}

@keyframes navHeight {
  0% {
    height: 0;
  }
  100% {
    height: 100%;
  }
}

.progress-nav li.active,
.progress-nav li.active ~ li {
  background: #cccccc;
}

.progress-nav li:before {
  height: 100%;
}

.progress-nav li.active ~ li:before {
  height: 0%;
}

